import React from "react";
import Layout from "../../layouts/default";
import PageHeader from "../../components/PageHeader";
import PageContent from "../../components/PageContent";
import TestimonialCard from "../../components/TestimonialCard";
import Link from "../../components/Link";
import testimonials from "./testimonials.yaml";

export default (props) => (
  <Layout>
    <PageHeader title="Testimonials">
      <p>
        See how much people love Bastion and what they have to say about it.
      </p>
      <p>
        If you want to share your thoughts to the world, tweet <Link
        to="https://twitter.com/TheBastionBot">@TheBastionBot</Link> and your
        tweets will be posted in this page.
      </p>
    </PageHeader>

    <PageContent>
      {
        testimonials.map((testimonial, i) => (
          <TestimonialCard
            user={ testimonial.user }
            community={ testimonial.community }
            text={ testimonial.text }
            link={ testimonial.link }
            key={ i }
          />
        ))
      }
    </PageContent>
  </Layout>
);
